<template>
  
    <audio ref="audio" src="http://dl.stream.qqmusic.qq.com/C400003OUlho2HcRHC.m4a?vkey=BEE93A6A37E5B35DEAE03D1248F25486D7356711BAF319ACEB64F47CF13C464869E2E56642777B3FE57D5C32BC440518D9D1D7F23B5FDDA8&guid=7281115656&uin=0&fromtag=66"></audio>
  </div>
</template>

<script type="text/ecmascript-6">
  import { mapGetters, mapMutations, mapActions } from 'vuex'

  export default {
    data() {
      return {
        songReady: false,
        currentTime: 0,
      }
    },
    methods:{
      ...mapMutations({
        setFullScreen: 'SET_FULL_SCREEN'
      }),
      ...mapActions([
        'savePlayHistory'
      ])
    },
    computed:{
      ...mapGetters([
        'currentIndex',
        'fullScreen',
        'playing',
        'currentSong'
      ])
    },
    watch: {
      currentSong(newSong, oldSong) {
        if (!newSong.id || !newSong.url || newSong.id === oldSong.id) {N
          return
        }
      // console.log(newSong)
        // this.$refs.audio.src = newSong.url
        // console.log(this.$refs.audio)
        this.$refs.audio.play()
        console.log(1)
      },
      playing(newPlaying) {
        if (!this.songReady) {
          return
        }
      }
    }
  }
</script>

